.header {
  width: 100%;
  padding: 10px 30px;
  background: #F0F0F0; }
  .header__title {
    padding-left: 54px; }
  .header__logo {
    width: 100%;
    max-width: 290px; }
  .header__btn {
    margin-right: 20px;
    background: #9D3C3C;
    color: #FFF;
    transition: all .5s ease; }
  .header__btn:hover {
    color: #FFF;
    background: #E83C3C; }
  .header__avatar {
    width: 44px;
    height: 44px;
    border-radius: 50%; }
  .header__menu {
    width: 22px;
    height: 22px;
    position: relative; }
  .header__menu span {
    display: block;
    transition: all .5s ease;
    background: #1B1B1B;
    height: 3px; }
  .header__menu span:nth-child(1) {
    width: 23px; }
  .header__menu span:nth-child(2) {
    width: 19px;
    margin-top: 3px; }
  .header__menu span:nth-child(3) {
    width: 13px;
    margin-top: 3px; }
  .header__menu.toggled span:nth-child(1) {
    width: 3px;
    height: 22px;
    transform: rotate(45deg);
    position: absolute;
    top: -1px;
    left: 10px; }
  .header__menu.toggled span:nth-child(2) {
    width: 3px;
    height: 22px;
    transform: rotate(-45deg);
    position: absolute;
    top: -4px;
    left: 10px; }
  .header__menu.toggled span:nth-child(3) {
    display: none; }

@media screen and (max-width: 768px) {
  .header {
    position: relative;
    z-index: 100;
    padding: 8px 16px; }
    .header__menu {
      margin-left: 16px; }
    .header__avatar {
      width: 28px;
      height: 28px; } }
