.mt-2rem {
  margin-top: 2rem !important; }

.current-page {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px; }

.card {
  position: relative;
  z-index: 20;
  border: none;
  min-height: 120px;
  color: #4E4E4E;
  min-width: 730px; }
  .card.shadow {
    box-shadow: 0px 10px 45px rgba(0, 0, 0, 0.1) !important; }
  .card .bordered {
    border-right: 1px solid #ECECEC; }
  .card__descr {
    font-size: 12px; }
  .card__descr b {
    font-weight: 700; }
  .card__descr:not(:first-child) {
    margin-top: 8px; }
  .card__price {
    text-align: center;
    font-size: 18px;
    font-weight: 700; }
  .card__form {
    display: flex;
    align-items: center; }
  .card__form-btn {
    font-size: 14px;
    font-weight: 700;
    margin-left: 25px; }
  .card__form-btn:hover {
    background-color: #9D3C3C; }
  .card__link {
    display: inline-block;
    margin-top: 8px; }

.card__form input[type="checkbox"] {
  display: none; }

.card__form-checkbox + .card__form-label {
  display: block;
  width: 25px;
  height: 25px;
  border: 1px solid #96999C;
  border-radius: 3px;
  margin: 0; }

.card__form-checkbox:checked + .card__form-label {
  background: url(./static/checked-icon.png) no-repeat center center; }

.switch-tariff__descr {
  margin-right: 40px;
  font-size: 14px;
  font-weight: 500; }

.switch-tariff__list {
  display: flex; }

.switch-tariff__list-item {
  padding: 20px 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #FFF;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.05); }

.switch-tariff__list-item:not(:last-child) {
  margin-right: 20px; }

.switch-tariff__list-img {
  width: 60px;
  height: 60px; }

.switch-tariff__list-title {
  font-size: 18px;
  font-weight: 900;
  color: #4E4E4E;
  text-align: center; }

.switch-tariff__list-link {
  margin-top: 6px;
  border-bottom: 1px solid #E83C3C; }

.order-modal {
  max-width: 800px; }
  .order-modal__content {
    background: #FFF; }
  .order-modal__header {
    max-width: 563px; }
  .order-modal__title {
    font-size: 18px;
    font-weight: 800;
    color: #4E4E4E; }
  .order-modal__header-txt {
    margin-top: 16px;
    font-size: 14px;
    font-weight: 500; }
  .order-modal .close {
    position: absolute;
    top: 16px;
    right: 16px; }
  .order-modal .close span {
    font-size: 45px;
    line-height: 0.5; }
  .order-modal__body {
    margin-top: 45px; }
  .order-modal__price {
    min-height: 100px;
    border: 1px solid #BEBEBE;
    font-size: 18px;
    font-weight: 700;
    color: #4E4E4E;
    text-align: center; }
  .order-modal__txt {
    max-width: 350px;
    font-size: 12px;
    line-height: 1.6; }
  .order-modal__paid-services {
    font-size: 14px;
    font-weight: 500; }
  .order-modal__btn {
    width: 49px;
    height: 49px;
    border: 1px solid #E83C3C;
    border-radius: 50%;
    margin-right: 10px;
    color: #E83C3C;
    font-size: 14px;
    font-weight: 700; }
  .order-modal__btn:hover {
    background: #E83C3C;
    color: #FFF; }
  .order-modal__footer {
    margin-top: 34px; }
  .order-modal__footer p {
    margin-top: 12px; }
  .order-modal__confirm-btn {
    width: 280px;
    height: 50px;
    color: #FFF;
    background-color: #9D3C3C;
    border-radius: 35px;
    margin-top: 20px; }

@media screen and (max-width: 1400px) {
  .add-obj-btn {
    width: 120px;
    height: 120px;
    font-size: 12px; }
  .card {
    min-width: auto; } }

@media screen and (max-width: 1200px) {
  .switch-tariff__list {
    flex-direction: column; }
  .switch-tariff__list-item:not(:last-child) {
    margin-right: 0; }
  .switch-tariff__list-item:first-child {
    margin-top: 18px; }
  .switch-tariff__list-item {
    margin-top: 36px; } }

@media screen and (max-width: 576px) {
  .add-obj-btn {
    width: 80px;
    height: 80px;
    font-size: 12px; } }
